.stalker {
  pointer-events: none;
  position: fixed;
  top: -8px;
  left: -8px;
  width: 24px;
  height: 24px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  transition: transform 0.2s, top, 0.5s, left 0.5s, width 0.5s, height 0.5s, background-color 0.5s;
  transition-timing-function: ease-out;
  z-index: 999;
}
.stalker.hov_ {
  top: -32px;
  left: -32px;
  width: 40px;
  height: 40px;
  transition: 0.5s;
  border: 1px solid #333;
  background: rgba(0, 0, 0, 0);
}

.bottomLine {
  width: 90%;
  height: 1px;
  position: absolute;
  bottom: 11%;
  left: 50%;
  transform: translateX(-50%);
  background: #333;
}

.scroll-child {
  background-color: azure;
}

.jpn {
  display: none;
}

.scroll-wrapper {
  height: 100vh;
}

#mouse-stalker {
  pointer-events: none;
  position: fixed;
  top: -10px;
  left: -10px;
  width: 20px;
  height: 20px;
  border: 1px solid #333;
  border-radius: 50%;
  transform: translate(0, 0);
  transition: transform 0.2s;
  transition-timing-function: ease-out;
  z-index: 999;
}
#mouse-stalker.is_active {
  top: -40px;
  left: -40px;
  width: 80px;
  height: 80px;
  transition: 0.2s;
}

